main {
	min-height: 100vh;
}

.boxContainer {
	display: flex;
	justify-content: space-between;
}

.boxLeft {
	width: 55%;
	height: 100%;
}

.boxRight {
	width: 42%;
	height: 100%;
}

.boxLeft span,
.boxRight span {
	white-space: nowrap;
}

@media only screen and (max-width:992px) {
	.boxContainer {
		display: flex;
		flex-direction: column;
	}

	.boxLeft {
		width: 100%;
		height: 100%;
		margin-bottom: 2rem;
	}

	.boxRight {
		width: 100%;
		height: 100%;
	}

	.displayNone {
		display: none;
	}

	.textCenter {
		text-align: center;
	}
}